'use server';

import { prisma } from '@/lib/prisma';
import { revalidatePath } from 'next/cache';

const DEFAULT_RATE = 2400; // Taux par défaut
const CURRENCY_KEY = 'USD_CDF';

/**
 * Récupère le taux de change actuel USD→CDF
 */
export async function getExchangeRate() {
  try {
    const client = prisma as unknown as {
      exchangeRate: {
        findUnique: (args: unknown) => Promise<{ id: string; rate: number; updatedAt: Date } | null>;
        create: (args: unknown) => Promise<{ id: string; rate: number; updatedAt: Date }>;
      };
    };

    const existing = await client.exchangeRate.findUnique({ where: { currency: CURRENCY_KEY } });
    if (existing) {
      return existing;
    }

    return client.exchangeRate.create({
      data: {
        currency: CURRENCY_KEY,
        rate: DEFAULT_RATE,
      },
    });
  } catch (error) {
    console.error('Erreur lors de la récupération du taux:', error);
    return { id: '', rate: DEFAULT_RATE, currency: CURRENCY_KEY, createdAt: new Date(), updatedAt: new Date() };
  }
}

/**
 * Met à jour le taux de change et recalcule tous les prix en CDF
 */
export async function updateExchangeRate(newRate: number, updatedBy?: string) {
  try {
    if (!newRate || newRate <= 0) {
      throw new Error('Le taux doit être un nombre positif.');
    }

    const client = prisma as unknown as {
      exchangeRate: {
        findUnique: (args: unknown) => Promise<{ id: string; rate: number; updatedAt: Date } | null>;
        upsert: (args: unknown) => Promise<{ id: string; rate: number; updatedAt: Date }>;
      };
      medication: {
        findMany: (args: unknown) => Promise<Array<Record<string, unknown>>>;
        update: (args: unknown) => Promise<void>;
      };
      auditLog: {
        create: (args: unknown) => Promise<void>;
      };
    };

    const existing = await client.exchangeRate.findUnique({
      where: { currency: CURRENCY_KEY },
    });
    const currentRate = existing?.rate ?? DEFAULT_RATE;

    const exchangeRecord = await client.exchangeRate.upsert({
      where: { currency: CURRENCY_KEY },
      create: {
        currency: CURRENCY_KEY,
        rate: newRate,
      },
      update: {
        rate: newRate,
      },
    });

    const medications = await client.medication.findMany({
      select: {
        id: true,
        price: true,
        purchasePrice: true,
        priceUsd: true,
        purchasePriceUsd: true,
      },
    });

    let medicationsUpdated = 0;
    const CHUNK_SIZE = 50;

    for (let start = 0; start < medications.length; start += CHUNK_SIZE) {
      const slice = medications.slice(start, start + CHUNK_SIZE);

      const updates = slice.map((medication) => {
        const med = medication as unknown as {
          id: string;
          price: number;
          purchasePrice: number;
          priceUsd?: number | null;
          purchasePriceUsd?: number | null;
        };

        const baseSalesUsd =
          med.priceUsd && med.priceUsd > 0 ? med.priceUsd : (med.price || 0) / currentRate;
        const basePurchaseUsd =
          med.purchasePriceUsd && med.purchasePriceUsd > 0
            ? med.purchasePriceUsd
            : (med.purchasePrice || 0) / currentRate;

        return client.medication.update({
          where: { id: med.id },
          data: {
            priceUsd: Math.round(baseSalesUsd * 100) / 100,
            purchasePriceUsd: Math.round(basePurchaseUsd * 100) / 100,
            price: Math.round(baseSalesUsd * newRate * 100) / 100,
            purchasePrice: Math.round(basePurchaseUsd * newRate * 100) / 100,
          },
        });
      });

      await Promise.all(updates);
      medicationsUpdated += slice.length;
    }

    if (updatedBy) {
      try {
        await client.auditLog.create({
          data: {
            action: 'exchange_rate_update',
            model: 'ExchangeRate',
            recordId: exchangeRecord.id,
            userId: updatedBy,
            oldValue: existing ? JSON.stringify({ rate: existing.rate }) : null,
            newValue: JSON.stringify({ rate: newRate, medicationsUpdated }),
          },
        });
      } catch (auditError) {
        console.warn("Impossible d'enregistrer le journal d'audit:", auditError);
      }
    }

    revalidatePath('/admin/inventory');
    revalidatePath('/admin');
    revalidatePath('/(app)/medications');

    return { ...exchangeRecord, medicationsUpdated };
  } catch (error) {
    console.error('Erreur lors de la mise à jour du taux:', error);
    throw error;
  }
}
