



'use server';

import { prisma } from '@/lib/prisma';

export async function getDashboardStats() {
  try {
    const startOfDay = new Date();
    startOfDay.setHours(0, 0, 0, 0);

    let totalRevenue = 0;
    let todaySales = 0;
    let totalClients = 0;

    // Essayer de charger les stats
    try {
      const revenueResult = await Promise.race([
        prisma.sale.aggregate({
          _sum: {
            totalAmount: true,
          },
        }),
        new Promise((_, reject) => 
          setTimeout(() => reject(new Error('BD timeout')), 5000)
        )
      ]);
      totalRevenue = (revenueResult as any)._sum.totalAmount || 0;
    } catch (error) {
      console.warn('[admin] Erreur sale.aggregate:', error);
      // Fallback si la table Sale n'existe pas
      totalRevenue = 0;
    }

    try {
      todaySales = await Promise.race([
        prisma.sale.count({
          where: {
            date: {
              gte: startOfDay,
            },
          },
        }),
        new Promise<number>((_, reject) => 
          setTimeout(() => reject(new Error('BD timeout')), 5000)
        )
      ]) as unknown as number;
    } catch (error) {
      console.warn('[admin] Erreur sale.count:', error);
      todaySales = 0;
    }

    try {
      totalClients = await Promise.race([
        prisma.client.count(),
        new Promise<number>((_, reject) => 
          setTimeout(() => reject(new Error('BD timeout')), 5000)
        )
      ]) as unknown as number;
    } catch (error) {
      console.warn('[admin] Erreur client.count:', error);
      totalClients = 0;
    }

    // Force le compteur de médicaments en rupture à zéro
    const outOfStock = 0;

    return {
      totalRevenue,
      todaySales,
      totalClients,
      outOfStock,
    };
  } catch (error) {
    console.error('[admin] Erreur finale getDashboardStats:', error);
    // Retourner des données par défaut
    return {
      totalRevenue: 0,
      todaySales: 0,
      totalClients: 0,
      outOfStock: 0,
    };
  }
}
