import Link from 'next/link';
import { ArrowRight, Clock, Coins } from 'lucide-react';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { getExchangeRate } from '@/lib/actions/exchange-rate';

export async function ExchangeRateDashboardCard() {
  const exchangeRate = await getExchangeRate();
  const rateValue = exchangeRate?.rate ?? 0;
  const updatedAt = exchangeRate?.updatedAt ? new Date(exchangeRate.updatedAt) : null;
  const formattedRate = new Intl.NumberFormat('fr-FR').format(rateValue);
  const updatedLabel = updatedAt
    ? `Mise à jour ${updatedAt.toLocaleDateString('fr-FR')} à ${updatedAt.toLocaleTimeString('fr-FR')}`
    : 'Jamais mis à jour';

  return (
    <Card className="bg-white">
      <CardHeader className="flex flex-row items-start justify-between gap-2">
        <div>
          <CardTitle className="flex items-center gap-2 text-lg">
            <Coins className="h-5 w-5 text-blue-600" />
            Taux du jour
          </CardTitle>
          <CardDescription>Conversion USD → CDF utilisée pour les prix</CardDescription>
        </div>
        <Link href="/admin/exchange-rate">
          <Button variant="outline" className="cursor-pointer">
            Gérer
          </Button>
        </Link>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="text-3xl font-semibold text-blue-700">
          {formattedRate}
          <span className="ml-2 text-base text-muted-foreground">CDF</span>
        </div>
        <div className="flex items-center gap-2 text-sm text-muted-foreground">
          <Clock className="h-4 w-4" />
          {updatedLabel}
        </div>
        <p className="text-sm text-gray-600">
          Les prix de vente et d&apos;achat sont recalculés automatiquement à chaque changement de taux.
        </p>
      </CardContent>
      <CardFooter>
        <Link
          href="/admin/exchange-rate"
          className="inline-flex items-center gap-1 text-sm font-medium text-blue-600 hover:underline"
        >
          Ouvrir le module
          <ArrowRight className="h-4 w-4" />
        </Link>
      </CardFooter>
    </Card>
  );
}
